<template>
  <div class="headerTitle">
    <div
      class="title-item"
      v-for="item in titleList"
      :key="item.value"
      :class="item.value == titleActive ? 'active' : ''"
    >
      <template v-if="item.value == 'modules'">
        <a-popover trigger="focus" overlayClassName="module-content">
          <template #content>
            <div
              class="module-item"
              v-for="item in list"
              :key="item.id"
              @click="handleClick(item)"
            >
              {{ item.title }}
            </div>
          </template>
          <a-button type="text">{{ item.label }}</a-button>
        </a-popover>
      </template>
      <a-button type="text" v-else>{{ item.label }}</a-button>
    </div>
  </div>
</template>
<script setup>
import { ref, reactive, onMounted } from "vue";
defineProps({
  list: [Array],
  default: () => {
    return [];
  },
});
let titleActive = ref("home");
let titleList = reactive([
  { label: "首页", value: "home" },
  { label: "模块", value: "modules" },
]);
onMounted(() => {
  //   setTimeout(() => {
  //     let attrs = useAttrs();
  //     console.log(attrs);
  //   }, 5000);
});
function handleClick(item) {
  let anchorElement = document.getElementById(item.id);
  /*如果对应id的锚点存在，就跳转到锚点*/
  if (anchorElement) {
    anchorElement.scrollIntoView();
    // anchorElement.scrollIntoView({
    //   behavior: "instant",
    //   block: "center",
    //   inline: "nearest",
    // });//浏览器兼容性问题，详情请查看https://developer.mozilla.org/zh-CN/docs/Web/API/Element/scrollIntoView
  }
}
</script>
<style lang="scss" scoped>
.headerTitle {
  display: flex;
  align-items: center;
  .title-item {
    .ant-btn {
      color: #383838;
      font-size: 1.2rem;
      font-weight: 550;
      margin: 0 10px;
      background-color: transparent;
      border: none;
      padding: 0;
      &:hover {
        color: rgb(95, 95, 95);
      }
    }
    &.active {
      .ant-btn {
        color: #293be4 !important;
      }
    }
  }
}
</style>
<style lang="scss">
.module-content {
  .ant-popover-content {
    .ant-popover-arrow:before {
      background-color: rgb(97, 166, 105);
    }
    .ant-popover-inner {
      padding: 12px 0;
      background: linear-gradient(
        to right,
        rgb(97, 166, 105),
        rgb(23, 160, 173)
      );
      .module-item {
        color: #fff;
        font-size: 1rem;
        padding: 4px 10px;
        cursor: pointer;
        border-radius: 4px;
        &:hover {
          background: linear-gradient(
            to right,
            rgb(35, 104, 151),
            rgb(82, 100, 128)
          );
        }
      }
    }
  }
}
</style>